.gallery-container {
    max-width: 100%;
    margin: 0 auto;
    padding: 20px;
    box-sizing: border-box;
}

.carousel {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    position: relative;
}

.carousel .slide {
    background: none;
    padding-bottom: 60px; /* Add space for legend and status */
}

.carousel .slide img {
    max-height: calc(80vh - 60px); /* Adjust for added padding */
    width: auto;
    max-width: 100%;
    object-fit: contain;
    position: relative;
    z-index: 1;
}

.carousel .legend {
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    padding: 10px;
    font-size: 16px;
    bottom: 60px; /* Position above the status */
    transition: all .5s ease-in-out;
    max-width: 80%;
    margin: 0 auto;
    border-radius: 10px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
}

.carousel .control-arrow {
    font-size: 26px;
    top: 50%;
    transform: translateY(-50%);
    width: 50px;
    height: 50px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 3;
}

.carousel .control-prev.control-arrow {
    left: 20px;
}

.carousel .control-next.control-arrow {
    right: 20px;
}

.carousel .control-prev.control-arrow:before {
    border-right: 12px solid #fff;
    margin-left: -6px;
}

.carousel .control-next.control-arrow:before {
    border-left: 12px solid #fff;
    margin-right: -6px;
}

/* Ensure proper spacing for the selector */
.carousel .carousel-status {
    position: absolute;
    bottom: 20px;
    right: 20px;
    padding: 5px 10px;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    border-radius: 5px;
    font-size: 12px;
    z-index: 2;
}

/* Media Queries */
@media (max-width: 1024px) {
    .carousel .slide img {
        max-height: calc(70vh - 60px);
    }
}

@media (max-width: 768px) {
    .gallery-container {
        padding: 10px;
    }

    .carousel .slide {
        padding-bottom: 100px;
    }

    .carousel .slide img {
        max-height: calc(60vh - 100px);
    }

    .carousel .legend {
        font-size: 14px;
        bottom: 100px;
        max-width: 90%;
    }

    .carousel .control-arrow {
        width: 40px;
        height: 40px;
    }

    .carousel .carousel-status {
        bottom: 60px;
    }
}

@media (max-width: 480px) {
    .carousel .slide {
        padding-bottom: 120px;
    }

    .carousel .slide img {
        max-height: calc(50vh - 120px);
    }

    .carousel .legend {
        font-size: 12px;
        bottom: 120px;
        padding: 8px;
        max-width: 95%;
    }

    .carousel .control-arrow {
        width: 30px;
        height: 30px;
    }

    .carousel .control-prev.control-arrow {
        left: 10px;
    }

    .carousel .control-next.control-arrow {
        right: 10px;
    }

    .carousel .carousel-status {
        bottom: 80px;
        right: 10px;
    }
}

@media (max-width: 360px) {
    .carousel .slide {
        padding-bottom: 140px;
    }

    .carousel .slide img {
        max-height: calc(40vh - 140px);
    }

    .carousel .legend {
        bottom: 140px;
        font-size: 11px;
    }

    .carousel .carousel-status {
        bottom: 100px;
    }
}

/* Adjust the dots (selector) positioning */
.carousel .control-dots {
    bottom: 10px;
    margin: 0;
    padding: 0;
    text-align: center;
    width: 100%;
    z-index: 3;
}

.carousel .control-dots .dot {
    background: #fff;
    border-radius: 50%;
    box-shadow: 1px 1px 2px rgba(0,0,0,0.9);
    cursor: pointer;
    display: inline-block;
    height: 8px;
    margin: 0 8px;
    opacity: 0.8;
    width: 8px;
}

.carousel .control-dots .dot.selected {
    opacity: 1;
}